body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

[class^='RaLogin-main-'] {
  background-image: none!important;
  background-color: #f8f9fa;
}

[class^='RaLogin-main-'] .MuiPaper-root.MuiCard-root {
  border-radius: 10px;
  /*min-height: 280px;*/
}

[class^='RaLogin-avatar-'] {
  /*background-color: #009688;*/
  background: url(./logo.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  min-height: 50px;
}

[class^='RaLogin-avatar-'] .MuiAvatar-root.MuiAvatar-circle{
  visibility: hidden;
}

[class^='RaLogin-main-'] form > .MuiCardActions-root.MuiCardActions-spacing {
  margin-bottom: 10px;
}

[class^='RaLogin-main-'] .MuiInput-underline:after {
  border-bottom: 2px solid #009688;
}

[class^='RaLogin-main-'] .MuiFormLabel-root.Mui-focused {
  color: #009688;
}

[class^='RaLogin-main-'] .MuiButton-containedPrimary {
  background-color: #875A7B;
}

[class^='RaLogin-main-'] .MuiButton-containedPrimary:hover {
  background-color: rgb(94, 62, 86);
}
